import { QUERY_CLINIC } from "common/constants/enum";
import { CalendlyResponseData } from "common/api/calendly";
import { getCookiesValueByName } from "common/utils/cookie";
import {
    convertQueryStringToQueryObject,
    QueryObject,
} from "common/utils/queryString";

import { PrefillParameters } from "./CalendlyService.types";
import { CALENDLY_NODE_ID } from "./constants";

interface CalendlyNotificationQueryObjectProps extends CalendlyResponseData {
    clinicId: number;
}

export const removeCalendlyFromDom = () => {
    const calendlyDomElement = document.getElementById(CALENDLY_NODE_ID);

    if (calendlyDomElement) {
        calendlyDomElement.innerHTML = "";
    }
};

// Get's query parameters as object from query string
export const extractQueryObject = () => {
    if (typeof window === "undefined") {
        return {};
    }

    const queryString = location.search;
    let decodedQueryString = null;

    try {
        decodedQueryString = decodeURIComponent(queryString);
    } catch (err) {
        console.error(err);
    }

    if (!decodedQueryString) {
        return {};
    }

    return decodedQueryString
        .slice(1)
        .split("&")
        .reduce((acc: { [key: string]: string }, item) => {
            const [key, value] = item.split("=");
            acc[key] = value;

            return acc;
        }, {});
};

export const getDefaultPrefilDataForCalendlyFromQuery = (): PrefillParameters => {
    const {
        firstname,
        lastname,
        email,
        phone,
        location,
        location_city,
    } = extractQueryObject();

    return {
        name: `${firstname} ${lastname}`,
        email,
        phone,
        customAnswers: {
            a1: phone,
            a2: location || location_city,
        },
    };
};

export const getDefaultPrefilDataForCalendly = (): PrefillParameters => {
    let firstnameCookie = "";
    let lastnameCookie = "";
    let emailCookie = "";
    let phoneCookie = "";

    const userData = getUserDataFromQueryParameter();

    try {
        firstnameCookie = decodeURIComponent(
            getCookiesValueByName("firstname")
        );
        lastnameCookie = decodeURIComponent(getCookiesValueByName("lastname"));
        emailCookie = decodeURIComponent(getCookiesValueByName("email"));
        phoneCookie = decodeURIComponent(getCookiesValueByName("phone"));
    } catch (e) {
        console.error(e);
    }

    const firstname = userData.firstname || firstnameCookie;
    const lastname = userData.lastname || lastnameCookie;
    const email = userData.email || emailCookie;
    const phone = userData.phone || phoneCookie;

    return {
        name: `${firstname} ${lastname}`,
        email,
        phone,
        customAnswers: {
            a1: phone,
        },
    };
};

export const getUserDataFromQueryParameter = () => {
    const FIRSTNAME_QUERY_PARAMETER_KEY = "firstname";
    const LASTNAME_QUERY_PARAMETER_KEY = "lastname";
    const PHONE_QUERY_PARAMETER_KEY = "phone";
    const EMAIL_QUERY_PARAMETER_KEY = "email";

    if (typeof location === "undefined") {
        return {};
    }
    const queryObject = convertQueryStringToQueryObject(location.search);
    const firstname = queryObject[FIRSTNAME_QUERY_PARAMETER_KEY] || "";
    const lastname = queryObject[LASTNAME_QUERY_PARAMETER_KEY] || "";
    const phone = queryObject[PHONE_QUERY_PARAMETER_KEY] || "";
    const email = queryObject[EMAIL_QUERY_PARAMETER_KEY] || "";

    return {
        phone,
        email,
        lastname,
        firstname,
    };
};

export const getClinicIdFromQueryParameter = () => {
    if (typeof location === "undefined") {
        return null;
    }
    const queryObject = convertQueryStringToQueryObject(location.search);
    const clinicId = queryObject[QUERY_CLINIC];

    return Number(clinicId) || null;
};

// Getting params eventId and inviteeId from uri like:
// https://api.calendly.com/api/v2/scheduled_events/ABCDABCDABCDABCD/invitees/ABCDABCDABCDABCD
export const getCalendlyNotificationParams = (uri: string) => {
    const uriSplit = uri.split("/");

    return {
        eventId: uriSplit[uriSplit.length - 3],
        inviteeId: uriSplit[uriSplit.length - 1],
    };
};

export const convertCalendlyNotificationQueryObject = ({
    invitee,
    event,
    clinicId,
}: CalendlyNotificationQueryObjectProps) => {
    const {
        email,
        name,
        full_name,
        first_name,
        last_name,
        questions_and_answers,
        timezone,
    } = invitee;
    const { start_time, end_time } = event;

    const result: QueryObject = {
        invitee_email: email,
        event_start_time: start_time,
        event_end_time: end_time,
        invitee_full_name: name || full_name || `${first_name} ${last_name}`,
        timezone,
        clinic_id: clinicId.toString(),
    };

    questions_and_answers?.map((item, index) => {
        result[`answer_${index + 1}`] = item.answer;
    });

    return result;
};
