import { useState, FC, ChangeEvent, MouseEvent } from "react";
import classNames from "classnames/bind";

import { redirectWithFormValues } from "common/services/RedirectService";
import {
    convertQueryObjectToQueryString,
    QueryObject,
} from "common/utils/queryString";
import { useFetchCities } from "common/hooks/useFetchCities";
import { CityType, ClinicType } from "common/api/location";
import { getFilteredCities } from "common/utils/getFilteredCities";
import { getCookiesValueByName } from "common/utils/cookie";
import { COOKIES, VersionToUniversalForm } from "common/constants/enum";
import { LeadType, updateContact } from "common/api/contacts";
import { redirectAnywayPromise } from "common/utils/redirectAnywayPromise";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";

import { City } from "./components/City";
import { Input } from "./components/Input";
import content from "./content";
import { SelectClinicProps } from "./SelectClinic.types";
import styles from "./SelectClinic.module.scss";
import { ClinicList } from "../new/SelectWithSearch/components/ClinicList";
import { isHasCombinedClinics } from "../new/SelectWithSearch/helpers/isHasCombinedClinics";

const cx = classNames.bind(styles);

export const SelectClinic: FC<SelectClinicProps> = ({ version, language }) => {
    const cities = useFetchCities(version);
    const [city, setCity] = useState("");
    const [selectedCity, setSelectedCity] = useState<CityType | null>();

    // @ts-ignore
    const { title, placeholder } = content[version][language];
    const results = getFilteredCities<CityType>(city, cities, ["title"]);

    const isShowClinics = (city: CityType) =>
        city?.clinics.length > 1 || isHasCombinedClinics(city.clinics);

    const handleClick = (event: MouseEvent<HTMLElement>, cityObj: CityType) => {
        event.preventDefault();

        if (cityObj.id === selectedCity?.id) {
            setSelectedCity(null);
            return;
        }

        if (isShowClinics(cityObj)) {
            setSelectedCity(cityObj);
        } else {
            const { clinics, id, title } = cityObj;
            updateContactBeforeRedirect(clinics[0].id.toString(), id, title);
        }
    };

    const updateContactBeforeRedirect = async (
        id: string,
        cityId: number,
        cityName: string
    ) => {
        const email = decodeURIComponent(getCookiesValueByName(COOKIES.EMAIL));
        const phone = decodeURIComponent(getCookiesValueByName(COOKIES.PHONE));
        const firstName = decodeURIComponent(
            getCookiesValueByName(COOKIES.FIRST_NAME)
        );
        const clinicId = Number(id.toString().split("-")[0]);

        if (email && phone && firstName) {
            const isTeens = id.toString().split("-")[1] === ClinicType.Teens;

            const updateContactPromise = await updateContact({
                email,
                phone,
                firstName,
                cityId,
                clinicId: parseInt(id, 10),
                ...(isTeens && {
                    properties: {
                        lead_type: LeadType.Teens,
                    },
                }),
            });

            const race = Promise.race([
                redirectAnywayPromise,
                updateContactPromise,
            ]);

            race.then(() => {
                redirectWithFormValues(version, clinicId);
            });
        }

        const customerUuid = decodeURIComponent(
            getCookiesValueByName(COOKIES.CUSTOMER_UUID)
        );

        if (!customerUuid) {
            const queryString = convertQueryObjectToQueryString({
                location: cityName,
                ...(id && {
                    clinicId: id,
                }),
            } as QueryObject);

            window.location.href = `${
                VersionToUniversalForm[version]
            }?${encodeURI(queryString)}`;
        }
    };

    const handleChangeClinic = async ({
        id,
    }: {
        id: string;
        type: ClinicType;
    }) => {
        if (selectedCity) {
            updateContactBeforeRedirect(
                id,
                selectedCity.id,
                selectedCity.title
            );
        }
    };

    const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
        setCity(event.target.value);
    };

    return (
        <section className={cx("container")}>
            <Heading
                className={cx("title")}
                tag={HeadingTag.H2}
                type={HeadingType.S}
            >
                {title}
            </Heading>

            <Input onSearchCity={handleChange} placeholder={placeholder} />

            {city &&
                results.map(({ item }) => (
                    <>
                        <City
                            key={item.id.toString()}
                            version={version}
                            name={item.title}
                            onClick={(e) => handleClick(e, item)}
                        />
                        {selectedCity?.id === item.id &&
                            isShowClinics(selectedCity) && (
                                <ClinicList
                                    clinics={item.clinics}
                                    onChange={handleChangeClinic}
                                    value={""}
                                    language={language}
                                    hasPreventSelect={false}
                                    isLink
                                />
                            )}
                    </>
                ))}

            {!city &&
                cities.map((city) => (
                    <>
                        <City
                            key={city.id.toString()}
                            version={version}
                            name={city.title}
                            onClick={(e) => handleClick(e, city)}
                        />
                        {selectedCity?.id === city.id &&
                            isShowClinics(city) && (
                                <ClinicList
                                    clinics={city.clinics}
                                    onChange={handleChangeClinic}
                                    language={language}
                                    value={""}
                                    hasPreventSelect={false}
                                    isLink
                                />
                            )}
                    </>
                ))}
        </section>
    );
};
