import { AxiosResponse } from "axios";

import { axiosInstanceV2 } from "common/api/global";
import { handleApiError } from "common/api/helpers";

import {
    GetCalendlyEventDataType,
    CalendlyResponse,
    GetCalendlyUrlByCityIdResponse,
    GetCalendlyUrlByCityIdType,
    GetCalendlyUrlByClinicIdType,
    GetCalendlyUrlByClinicIdResponse,
    BLOCKED_CALENDLY_URL,
    GetCalendlySlotsByCityIdType,
    CreateCalendlySlotType,
    CalendlyDaysDataWrap,
} from "./calendly.types";

// We temporary use the production API URL, cause don't have id's of cities list on dev env
const PROD_API_URL = "https://api-v2.smile2impress.com";

export const getCalendlyEventData = async ({
    eventId,
    inviteeId,
}: GetCalendlyEventDataType) =>
    await axiosInstanceV2
        .get(
            `${PROD_API_URL}/booking-service/attraction/v1/scheduled-events/${eventId}/invitees/${inviteeId}`
        )
        .then(
            (response: AxiosResponse<CalendlyResponse>) => response?.data.data
        )
        .catch(handleApiError);

export const getCalendlyUrlByCityId = async ({
    cityId,
}: GetCalendlyUrlByCityIdType) =>
    await axiosInstanceV2
        .get(
            `${PROD_API_URL}/booking-service/attraction/v1/cities/${cityId}/one-time-booking-url/first`
        )
        .then(
            (response: AxiosResponse<GetCalendlyUrlByCityIdResponse>) =>
                response.data.data.url
        )
        .catch(handleApiError);

export const getCalendlyUrlByClinicId = async ({
    clinicId,
}: GetCalendlyUrlByClinicIdType) =>
    await axiosInstanceV2
        .get(
            `${PROD_API_URL}/attraction-service/v2/booking/clinics/${clinicId}/one-time-booking-url`
        )
        .then((response: AxiosResponse<GetCalendlyUrlByClinicIdResponse>) => {
            const url = response.data.data.calendlyUrl;

            if (url === BLOCKED_CALENDLY_URL) {
                return null;
            }

            return url;
        })
        .catch(handleApiError);

export const setExpireCalendlyStatusByClinicId = async ({
    clinicId,
}: GetCalendlyUrlByClinicIdType) =>
    await axiosInstanceV2
        .patch(
            `${PROD_API_URL}/attraction-service/v2/booking/clinics/${clinicId}/one-time-booking-url`,
            { expired: true }
        )
        .catch(handleApiError);

export const getCalendlySlotsByClinicId = async ({
    clinicId,
}: GetCalendlySlotsByCityIdType) =>
    await axiosInstanceV2
        .get(`${PROD_API_URL}/attraction-service/calendly/v3/getRange`, {
            params: {
                clinicId,
            },
        })
        .then((response: AxiosResponse<CalendlyDaysDataWrap>) => {
            return response?.data;
        })
        .catch(handleApiError);

export const createCalendlySlot = async (data: CreateCalendlySlotType) =>
    await axiosInstanceV2
        .post(`${PROD_API_URL}/attraction-service/calendly/v1/create`, data)
        .then((response: AxiosResponse<CalendlyResponse>) => response?.data)
        .catch(handleApiError);
