import { FC, useState, useRef, useEffect } from "react";
import classNames from "classnames/bind";
import Carousel, { ArrowProps, ResponsiveType } from "react-multi-carousel";

import { Picture } from "common/components/Picture";
import { WINDOW_SIZE } from "common/constants/enum";
import { Icon } from "common/components/Icons";
import ArrowLeftIcon from "common/components/Icons/icons/24/arrow-left.svg";
import { Tabs, TabType } from "common/components/Tabs";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Container } from "common/components/Container";
import { useWindowDimensions } from "common/hooks/useWindowDimensions";

import { SlidesImageType } from "content/remarketingC2Page/content.types";

import { SliderProps, DirectionArrow } from "./Slider.types";
import styles from "./Slider.module.scss";

const cx = classNames.bind(styles);

const responsive: ResponsiveType = {
    desktop: {
        breakpoint: {
            min: WINDOW_SIZE.DESKTOP,
            max: 3000,
        },
        items: 1,
    },
    mobile: {
        breakpoint: {
            min: 0,
            max: WINDOW_SIZE.DESKTOP - 1,
        },
        items: 1,
        paritialVisibilityGutter: 48,
    },
};

const Arrow: FC<ArrowProps & { direction: DirectionArrow }> = ({
    onClick,
    direction,
}) => {
    return (
        <Container className={cx("container-arrow")}>
            <button
                onClick={onClick}
                className={cx("arrow", {
                    "arrow-right": direction === DirectionArrow.RIGHT,
                    "arrow-left": direction === DirectionArrow.LEFT,
                })}
                type="button"
            >
                <Icon
                    className={cx("arrow-icon", {
                        "arrow-icon-right": direction === DirectionArrow.RIGHT,
                        "arrow-icon-left": direction === DirectionArrow.LEFT,
                    })}
                >
                    <ArrowLeftIcon />
                </Icon>
            </button>
        </Container>
    );
};

export const Slider: FC<SliderProps> = ({
    images,
    title,
    tabs,
    currentCity,
}) => {
    const carouselRef = useRef<Carousel>(null);

    const [activeCity, setActiveCity] = useState<TabType>();
    const [activeTab, setActiveTab] = useState<TabType>(tabs[0]);
    const [sortedTabs, setSortedTabs] = useState(tabs);

    const { width } = useWindowDimensions();
    const isDesktop = width > WINDOW_SIZE.DESKTOP - 1;

    useEffect(() => {
        if (activeCity) {
            resortTabs();
        }
    }, [activeCity]);

    useEffect(() => {
        if (currentCity) {
            const tabItem = tabs.find((item) => item.title === currentCity);
            if (tabItem) {
                setActiveCity(tabItem);
                setActiveTab(tabItem);
            }
        }
    }, [currentCity]);

    const onTabChanged = (option: TabType) => {
        setActiveTab(option);
        carouselRef?.current?.goToSlide(0);
    };

    const resortTabs = () => {
        if (activeCity?.value) {
            tabs.sort((a: TabType) => {
                if (a.value === activeCity.value) {
                    return -1;
                } else {
                    return 1;
                }
                return 0;
            });
            setSortedTabs(() => [...tabs]);
        }
    };

    return (
        <section className={cx("root")}>
            <div className={cx("header")}>
                {title && (
                    <Heading
                        className={cx("title")}
                        tag={HeadingTag.H4}
                        type={HeadingType.M}
                    >
                        {title}
                    </Heading>
                )}
                {sortedTabs?.length && sortedTabs?.length > 1 && (
                    <Tabs
                        classNameScroll={cx("tabs")}
                        tabs={sortedTabs}
                        onChange={onTabChanged}
                        initialTab={activeTab}
                    />
                )}
            </div>
            <Carousel
                className={cx("list")}
                sliderClass={cx("container")}
                itemClass={cx("item")}
                responsive={responsive}
                removeArrowOnDeviceType={"mobile"}
                customLeftArrow={<Arrow direction={DirectionArrow.LEFT} />}
                customRightArrow={<Arrow direction={DirectionArrow.RIGHT} />}
                showDots={!isDesktop}
                ref={carouselRef}
                infinite={isDesktop}
                centerMode={isDesktop}
                partialVisbile={!isDesktop}
            >
                {images[activeTab.value as keyof SlidesImageType]?.map(
                    (image, index) => {
                        return (
                            <Picture
                                {...image}
                                key={index}
                                classNameImg={cx("image")}
                            />
                        );
                    }
                )}
            </Carousel>
        </section>
    );
};
