import {
    FC,
    useState,
    useCallback,
    useEffect,
    useContext,
    useRef,
} from "react";
import classNames from "classnames/bind";
import { useRouter } from "next/router";
import { pathOr } from "ramda";

import { getContactV1 } from "common/api/contacts";
import { ErrorContext } from "common/context/errorContext";
import { ErrorType } from "common/components/ErrorLayout";
import { MaxWidthContainer } from "common/components/new/MaxWidthContainer";
import { AdvantagesGrid } from "common/components/AdvantagesGrid";
import { Container } from "common/components/Container";
import { BookingSteps, BookingStepType } from "common/components/BookingSteps";
import {
    CalendlyService,
    CALENDLY_MESSAGE,
} from "common/services/CalendlyService";
import { Link } from "common/components/Link";
import { Icon } from "common/components/Icons";
import LogoIcon from "common/components/Icons/icons/logo.svg";
import TeethIcon from "common/components/Icons/icons/48/teeth.svg";
import Scan3DDarkIcon from "common/components/Icons/icons/48/scan-3d-dark.svg";
import MaskIcon from "common/components/Icons/icons/48/mask.svg";
import { Text } from "common/components/Text";
import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { SocialMediaProps } from "common/components/SocialMedia";
import {
    Button,
    ButtonSize,
    ButtonTheme,
    mapThemeColorLoading,
} from "common/components/new/Button";
import { HeaderShort } from "common/components/new/HeaderShort";
import { LoadingIcon } from "common/components/Icons/LoadingIcon";
import { useIsIOS } from "common/hooks/useIsIOS";
import { TextWithIcon } from "common/components/new/TextWithIcon";
import { SelectClinic } from "common/components/SelectClinic";
import { pathConfig } from "common/configs/pathConfig";
import { useScriptDownload } from "common/hooks/useScriptDownload";
import {
    QUERY_CALENDLY_SUBSTRING,
    TwentyFourHoursInSeconds,
} from "common/constants/enum";
import { setCookieByObjectValues } from "common/utils/cookie";

import { Footer } from "components/Footer";

import { Slider } from "./components/Slider";
import styles from "./RemarketingC2Layout.module.scss";
import {
    RemarketingC2LayoutProps,
    RemarketingUserInfo,
} from "./RemarketingC2Layout.types";

const cx = classNames.bind(styles);

export const RemarketingC2Layout: FC<RemarketingC2LayoutProps> = ({
    content: {
        header,
        steps,
        contacts,
        advantagesBlock,
        advantages,
        slides,
        tabs,
        cta,
    },
    version,
    language,
}) => {
    // Load Yandex Metrika
    useScriptDownload("/special/scripts/yandex-metrika.js");

    const router = useRouter();
    const bookingRef = useRef(null);
    const { contact } = router?.query ?? {};

    const { setErrorType } = useContext(ErrorContext);

    const [userInfo, setUserInfo] = useState<RemarketingUserInfo | null>(null);

    const fetchContact = async (uuid: string) => {
        const { data } = (await getContactV1(uuid)) || {};
        const userContactData = data?.data;

        if (userContactData) {
            const {
                firstName,
                lastName,
                email,
                phone,
                city,
                calendlyUrl,
            } = userContactData;

            setUserInfo({
                firstName,
                lastName,
                email,
                phone,
                city,
                calendlyUrl,
            });

            const options = { "max-age": TwentyFourHoursInSeconds, path: "/" };
            setCookieByObjectValues(
                { firstname: firstName, lastname: lastName, phone, email },
                options
            );
        } else {
            setErrorType(ErrorType.API);
        }
    };

    useEffect(() => {
        if (contact) {
            fetchContact(contact as string);
        }
    }, [contact]);

    const [activeStep, setActiveStep] = useState<BookingStepType>(
        BookingStepType.FIRST
    );

    const isIOS = useIsIOS();

    const handleCalendlyMessage = useCallback((message: CALENDLY_MESSAGE) => {
        const hasWindow = typeof window !== "undefined";

        if (hasWindow && message === CALENDLY_MESSAGE.DATA_TIME_SELECTED) {
            setActiveStep(BookingStepType.SECOND);
        }

        if (hasWindow && message === CALENDLY_MESSAGE.EVENT_TYPE_VIEWED) {
            setActiveStep(BookingStepType.FIRST);
        }

        if (hasWindow && message === CALENDLY_MESSAGE.EVENT_SCHEDULED) {
            setActiveStep(BookingStepType.THIRD);
        }
    }, []);

    const calendlyContacts = {
        list: [contacts.phone, contacts.wa, contacts.email],
    } as SocialMediaProps;

    const calendlyPrefillObject = {
        name: `${userInfo?.firstName} ${userInfo?.lastName}`,
        email: userInfo?.email,
        phone: userInfo?.phone,
        customAnswers: {
            a1: userInfo?.phone || "",
            a2: userInfo?.city?.title || "",
        },
    };

    const calendlyQuery = QUERY_CALENDLY_SUBSTRING + userInfo?.city?.title;
    const buttonLink = userInfo?.city?.title
        ? `${pathConfig.universalAppointment[version]}${calendlyQuery}`
        : pathConfig.universalSelect[version];

    const scrollToBookingSection = () => {
        const offsetTop = pathOr(0, ["current", "offsetTop"], bookingRef);
        window.scrollTo({ top: offsetTop - 72, behavior: "smooth" });
    };

    const calendlyBlocked = userInfo?.calendlyUrl === "blocked";
    const showCalendar = userInfo !== null && userInfo?.calendlyUrl;
    const showSelectCity = userInfo !== null && !userInfo?.calendlyUrl;

    const renderIcon = (iconName?: string) => {
        switch (iconName) {
            case "teeth48":
                return <TeethIcon />;
            case "scan3DDark48":
                return <Scan3DDarkIcon />;
            case "mask48":
                return <MaskIcon />;
            default:
                return null;
        }
    };

    return (
        <MaxWidthContainer>
            <main className={cx("root")}>
                <section
                    className={cx("main", {
                        main_margin: isIOS,
                    })}
                >
                    <HeaderShort className={cx("header")} {...contacts} />
                    <Link className={cx("logo")} link={contacts?.site?.link}>
                        <Icon className={cx("logo__icon")}>
                            <LogoIcon />
                        </Icon>
                    </Link>
                    <div
                        className={cx("content", {
                            "content_with-paddings": isIOS,
                        })}
                    >
                        <Heading
                            tag={HeadingTag.H5}
                            type={HeadingType.M}
                            className={cx("main__subtitle")}
                        >
                            {header.subtitle}&nbsp;{userInfo?.firstName}!
                        </Heading>
                        <Heading
                            className={cx("main__title", {
                                main__title_margin: isIOS,
                            })}
                        >
                            {header.title}
                        </Heading>
                        {isIOS && (
                            <Button
                                link={buttonLink}
                                className={cx("main__button")}
                                size={ButtonSize.M}
                                isMaxWidth
                            >
                                {header.button.title}
                            </Button>
                        )}
                    </div>
                </section>
                {!isIOS && (
                    <div
                        ref={bookingRef}
                        className={cx("booking", {
                            error: calendlyBlocked,
                        })}
                    >
                        {userInfo === null && (
                            <LoadingIcon
                                stroke={
                                    mapThemeColorLoading[
                                        ButtonTheme.TRANSPARENT
                                    ]
                                }
                                className={cx("icon-loading")}
                            />
                        )}
                        {showCalendar && (
                            <div className={cx("calendar")}>
                                {!calendlyBlocked && (
                                    <div className={cx("calendar__head")}>
                                        <BookingSteps
                                            list={steps}
                                            activeStepName={activeStep}
                                        />
                                    </div>
                                )}
                                <CalendlyService
                                    className={cx("calendly", {
                                        error: calendlyBlocked,
                                    })}
                                    language={language}
                                    version={version}
                                    contacts={calendlyContacts}
                                    onMessage={handleCalendlyMessage}
                                    {...(!calendlyBlocked && {
                                        calendlyUrl: userInfo?.calendlyUrl,
                                    })}
                                    prefill={calendlyPrefillObject}
                                    currentCityId={userInfo?.city?.id}
                                />
                            </div>
                        )}
                        {showSelectCity && (
                            <Container className={cx("select-city")}>
                                <SelectClinic
                                    version={version}
                                    language={language}
                                />
                            </Container>
                        )}
                    </div>
                )}

                <div className={cx("container")}>
                    <div className={cx("grid")}>
                        <AdvantagesGrid
                            {...advantages}
                            version={version}
                            onButtonClick={scrollToBookingSection}
                            classNameTitle={cx("grid__title")}
                        />
                    </div>
                    {advantagesBlock && (
                        <div className={cx("advantages-block")}>
                            <Heading
                                tag={HeadingTag.H3}
                                className={cx("advantages-block__title")}
                            >
                                {advantagesBlock.title}
                            </Heading>
                            <Text className={cx("advantages-block__subtitle")}>
                                {advantagesBlock.subtitle}
                            </Text>
                            <div className={cx("advantages-block__list")}>
                                {advantagesBlock.list.map((item, index) => (
                                    <TextWithIcon
                                        className={cx("advantages-block__item")}
                                        iconClassName={cx(
                                            "advantages-block__icon"
                                        )}
                                        {...item}
                                        icon={renderIcon(item.icon as string)}
                                        key={index}
                                    />
                                ))}
                            </div>
                        </div>
                    )}
                </div>
                {slides && tabs && (
                    <Slider
                        tabs={tabs}
                        currentCity={userInfo?.city?.title}
                        {...slides}
                    />
                )}
                <section className={cx("cta", "only-desktop")}>
                    <Heading className={cx("cta__title")} tag={HeadingTag.H3}>
                        {cta.title}
                    </Heading>
                    <Button
                        size={ButtonSize.M}
                        {...(isIOS
                            ? { link: buttonLink }
                            : { onClick: scrollToBookingSection })}
                    >
                        {cta.button.title}
                    </Button>
                </section>
                <Container>
                    <Footer {...contacts} />
                </Container>
            </main>

            {/* Yandex.Metrika counter */}
            {process.env.ENV === "production" && (
                <div>
                    <img
                        src="https://mc.yandex.ru/watch/83092081"
                        style={{
                            position: "absolute",
                            left: "-9999px",
                        }}
                        alt="yandex metrika"
                    />
                </div>
            )}
            {/* /Yandex.Metrika counter */}
        </MaxWidthContainer>
    );
};
