import { FC, useEffect, useState } from "react";
import classNames from "classnames/bind";
import { nanoid } from "nanoid";

import {
    getCalendlyUrlByClinicId,
    setExpireCalendlyStatusByClinicId,
} from "common/api/calendly";
import { CITY_NAME_COOKIE } from "common/constants/enum";
import { getCookiesValueByName } from "common/utils/cookie";
import {
    TradedoublerIframe,
    getValueOfCookieTduid,
    setValueOfCookieTduid,
} from "common/services/TradedoublerPixelService";
import { Heading } from "common/components/new/Heading";
import { getUtms } from "common/services/UtmService";
import { Icon } from "common/components/Icons";
import Calendar6Icon from "common/components/Icons/icons/48/calendar-6.svg";
import { LoadingIcon } from "common/components/Icons/LoadingIcon";
import { ThanksFooter } from "common/components/ThanksFooter";
import {
    ButtonTheme,
    mapThemeColorLoading,
} from "common/components/new/Button";
import { useScriptDownload } from "common/hooks/useScriptDownload";

import { CALENDLY_MESSAGE, CALENDLY_NODE_ID } from "./constants";
import { removeCalendlyFromDom } from "./helpers";
import {
    useCalendlyNotification,
    useSubmitAnalyticEventOnEventScheduledMessage,
    useCalendlyEventRedirect,
} from "./hooks";
import content from "./content";
import {
    CalendlyContentProps,
    CalendlyServiceProps,
    WindowWithCalendly,
} from "./CalendlyService.types";
import styles from "./CalendlyService.module.scss";

const cx = classNames.bind(styles);

const leadNumber = nanoid();

export const CalendlyService: FC<CalendlyServiceProps> = ({
    version,
    language,
    contacts,
    prefill = {},
    className,
    onMessage = () => {},
    calendlyUrl,
    clinicId,
}) => {
    const hasWindow = typeof window !== "undefined";
    const {
        message: calendlyMessage,
        eventId,
        inviteeId,
    } = useCalendlyNotification();
    const contentByVersion = content[language];

    const { title, description } = contentByVersion as CalendlyContentProps;
    const [currentCalendlyUrl, setCalendlyUrl] = useState<
        string | null | undefined
    >(calendlyUrl);
    const [hasExpireStatus, setExpireStatus] = useState(false);
    const [currentTduid, setTduid] = useState<string>();
    const isCalendlyLoaded = CALENDLY_MESSAGE.INIT !== calendlyMessage;

    useSubmitAnalyticEventOnEventScheduledMessage(calendlyMessage);

    useEffect(() => {
        const fetchCalendlyUrl = async () => {
            if (clinicId) {
                const url = await getCalendlyUrlByClinicId({
                    clinicId,
                });

                setCalendlyUrl(url || null);

                if (!url) {
                    onMessage(CALENDLY_MESSAGE.ERROR);
                }
            } else {
                setCalendlyUrl(null);
                onMessage(CALENDLY_MESSAGE.ERROR);
            }
        };

        if (typeof calendlyUrl === "undefined") {
            fetchCalendlyUrl();
        }
    }, [clinicId]);

    useCalendlyEventRedirect({
        eventId,
        inviteeId,
        clinicId,
        hasExpireStatus,
        version,
    });

    useEffect(() => {
        if (eventId && inviteeId && clinicId) {
            const setExpireCalendlyStatus = async () => {
                await setExpireCalendlyStatusByClinicId({
                    clinicId,
                });
                setExpireStatus(true);
            };

            setExpireCalendlyStatus();
        }
    }, [eventId, inviteeId, clinicId]);

    useEffect(() => {
        onMessage(calendlyMessage);
    }, [calendlyMessage]);

    const isScriptLoaded = useScriptDownload(
        "https://assets.calendly.com/assets/external/widget.js"
    );

    useEffect(() => {
        if (isScriptLoaded && currentCalendlyUrl) {
            if ((window as WindowWithCalendly).Calendly) {
                const cityName = getCookiesValueByName(CITY_NAME_COOKIE);
                const url = `${currentCalendlyUrl}?a2=${cityName}&hide_event_type_details=1&hide_gdpr_banner=1`;
                (window as WindowWithCalendly).Calendly.initInlineWidget({
                    url,
                    parentElement: document.getElementById(CALENDLY_NODE_ID),
                    prefill,
                    utm: getUtms(),
                });
            } else {
                console.error("Calendly widget didn't loaded!");
            }
        }

        return removeCalendlyFromDom;
    }, [isScriptLoaded, currentCalendlyUrl]);

    useEffect(() => {
        if (!hasWindow) {
            return;
        }

        const tduid = getValueOfCookieTduid();
        if (tduid) {
            setValueOfCookieTduid();
            setTduid(tduid);
        }
    }, [hasWindow]);

    return (
        <>
            {currentCalendlyUrl === null && (
                <div className={cx("calendly-error")}>
                    <div className={cx("calendly-error-content")}>
                        <Icon className={cx("calendly-error-icon")}>
                            <Calendar6Icon />
                        </Icon>
                        <Heading className={cx("calendly-error-title")}>
                            {title}
                        </Heading>
                        <div className={cx("calendly-error-description")}>
                            {description}
                        </div>

                        <ThanksFooter
                            {...contacts}
                            className={cx("calendly-error-contacts")}
                        />
                    </div>
                </div>
            )}

            <div
                className={cx(
                    "calendly-wrapper",
                    className,
                    isCalendlyLoaded && "calendly-wrapper_is-loaded",
                    currentCalendlyUrl === null && "calendly-wrapper_has-error"
                )}
                data-auto-load="false"
            >
                <div className={cx("calendly")} id={CALENDLY_NODE_ID}></div>
            </div>

            {!isCalendlyLoaded && currentCalendlyUrl !== null && (
                <LoadingIcon
                    stroke={mapThemeColorLoading[ButtonTheme.TRANSPARENT]}
                    className={cx("icon-loading")}
                />
            )}

            {currentTduid && (
                <TradedoublerIframe
                    tduid={currentTduid}
                    leadNumber={leadNumber}
                />
            )}
        </>
    );
};
