import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import { VersionToUniversalForm } from "common/constants/enum";
import {
    convertQueryObjectToQueryString,
    QueryObject,
} from "common/utils/queryString";
import { Icon } from "common/components/Icons";
import RightIcon from "common/components/Icons/icons/24/right.svg";

import { CityProps } from "./City.types";
import styles from "./City.module.scss";

const cx = classNames.bind(styles);

export const City: FunctionComponent<CityProps> = ({
    version,
    name,
    onClick,
}) => {
    const queryString = convertQueryObjectToQueryString({
        location: name,
    } as QueryObject);

    return (
        <div className={cx("root")}>
            <a
                href={`${VersionToUniversalForm[version]}?${encodeURI(
                    queryString
                )}`}
                onClick={onClick}
                className={cx("link")}
            >
                <span>{name}</span>
                <Icon>
                    <RightIcon />
                </Icon>
            </a>
        </div>
    );
};
