import { useEffect, useState } from "react";
import { customAlphabet } from "nanoid";

export const useScriptDownload = (url: string) => {
    const [isScriptLoaded, setScriptIsLoaded] = useState(false);
    // Basic nanoid alphabet is not valid for Element attributes
    const nanoid = customAlphabet("abcdefghijklmnopqrstuvwxyz", 10);

    useEffect(() => {
        const ID = nanoid();

        const script = document.createElement("script");
        script.id = ID;
        script.src = url;
        script.async = true;
        script.onload = () => setScriptIsLoaded(true);
        document.body.appendChild(script);

        return () => {
            const loadedScript = document.querySelector(`#${ID}`);

            if (loadedScript) {
                document.body.removeChild(loadedScript);
            }
        };
    }, []);

    return isScriptLoaded;
};
