import { FunctionComponent } from "react";
import classNames from "classnames/bind";

import {
    Heading,
    HeadingTag,
    HeadingType,
} from "common/components/new/Heading";
import { Link } from "common/components/Link";
import {
    getAnalyticsId,
    GetAnalyticsIdElement,
} from "common/utils/AnalyticsId";
import { Icon } from "common/components/Icons";
import { SocialMediaIcon } from "common/components/Icons/SocialMediaIcon";

import { ThanksFooterProps, ContactLink } from "./ThanksFooter.types";
import styles from "./ThanksFooter.module.scss";

const cx = classNames.bind(styles);

export const ThanksFooter: FunctionComponent<ThanksFooterProps> = ({
    list = [],
    title,
    className,
}) => {
    return (
        <div className={cx(className, "root")}>
            {title && (
                <Heading
                    tag={HeadingTag.H2}
                    type={HeadingType.M}
                    className={cx("title")}
                >
                    {title}
                </Heading>
            )}
            <ul className={cx("list")}>
                {list.map(({ id, name, icon, link }: ContactLink) => (
                    <li className={cx("list__item")} key={link}>
                        <Link
                            id={getAnalyticsId({
                                zone: "footer",
                                element: GetAnalyticsIdElement.LINK,
                                label: id,
                            })}
                            className={cx("link")}
                            link={link}
                        >
                            {name}
                            <Icon className={cx("icon")}>
                                <SocialMediaIcon iconName={icon} />
                            </Icon>
                        </Link>
                    </li>
                ))}
            </ul>
        </div>
    );
};
