import { useEffect, useState } from "react";
import { nanoid } from "nanoid";
import { sha256 } from "js-sha256";

import { postFacebookEvent } from "common/api/facebook";
import { getCookiesValueByName } from "common/utils/cookie";
import {
    sendFacebookEvent,
    FacebookEvents,
    sendGTMEvent,
    GTMEvents,
    sendSnapchatPixelEvent,
    SnapchatPixelEvents,
} from "common/utils/metrics";
import {
    convertCalendlyNotificationQueryObject,
    getCalendlyNotificationParams,
    getDefaultPrefilDataForCalendly,
} from "common/services/CalendlyService/helpers";
import {
    COOKIES,
    CountryVersion,
    VersionToNewThanksPage,
} from "common/constants/enum";
import { getCalendlyEventData } from "common/api/calendly";
import {
    convertQueryObjectToQueryString,
    QueryObject,
} from "common/utils/queryString";

import { CALENDLY_MESSAGE } from "./constants";
import { CalendlyWidgetEvent } from "./CalendlyService.types";

const isCalendlyEvent = (message: CalendlyWidgetEvent) => {
    return message.data.event
        ? message.data.event.indexOf("calendly") === 0
        : false;
};

interface CalendlyNotificationType {
    message: CALENDLY_MESSAGE;

    clinicId?: number;
    eventId?: string;
    inviteeId?: string;
}

// Messages that Calendly send when something happens with the widget
// @see https://help.calendly.com/hc/en-us/articles/360020052833-Advanced-embed-options#3
export const useCalendlyNotification = (): CalendlyNotificationType => {
    const [
        notificationData,
        setNotificationData,
    ] = useState<CalendlyNotificationType>({ message: CALENDLY_MESSAGE.INIT });

    const onCalendlyEvent = (e: CalendlyWidgetEvent) => {
        const { event: message, payload } = e.data;

        if (isCalendlyEvent(e)) {
            const data: CalendlyNotificationType = { message };

            if (payload.invitee) {
                const { eventId, inviteeId } = getCalendlyNotificationParams(
                    payload.invitee.uri
                );
                data.eventId = eventId;
                data.inviteeId = inviteeId;
            }

            setNotificationData(data);
        }
    };

    useEffect(() => {
        window.addEventListener("message", onCalendlyEvent);

        return () => {
            window.removeEventListener("message", onCalendlyEvent);
        };
    }, []);

    return notificationData;
};

export const useSubmitAnalyticEventOnEventScheduledMessage = (
    calendlyMessage: CALENDLY_MESSAGE
) => {
    useEffect(() => {
        const defaultPrefilObject = getDefaultPrefilDataForCalendly();
        const { name, email, phone } = defaultPrefilObject;
        const fullName = name?.split(" ");

        const sendConversionApiEvent = async (eventID: string) => {
            await postFacebookEvent({
                eventName: FacebookEvents.FORM_SUBMIT_FACEBOOK_SCHEDULED_EVENT,
                eventSourceUrl: location.href,
                eventId: eventID,
                userData: {
                    firstName: fullName && fullName[0].toLowerCase(),
                    lastName: fullName?.slice(1).join(" ").toLowerCase(),
                    email,
                    phone,
                    fbp: decodeURIComponent(getCookiesValueByName(COOKIES.FBP)),
                    fbc: decodeURIComponent(getCookiesValueByName(COOKIES.FBC)),
                },
            });
        };

        if (calendlyMessage === CALENDLY_MESSAGE.EVENT_TYPE_VIEWED) {
            sendGTMEvent(GTMEvents.CALENDLY_FORM_VIEWED_GTM_EVENT);
        }

        if (calendlyMessage === CALENDLY_MESSAGE.DATA_TIME_SELECTED) {
            sendGTMEvent(GTMEvents.CALENDLY_FORM_DATA_TIME_SELECTED_GTM_EVENT);
        }

        if (calendlyMessage === CALENDLY_MESSAGE.EVENT_SCHEDULED) {
            // Events fire
            const eventID = nanoid();
            const emailToLowerCase = email?.toLowerCase();
            const phoneToLowerCase = phone?.toLowerCase();

            sendSnapchatPixelEvent(
                SnapchatPixelEvents.SNAPCHAT_PIXEL_SCHEDULED_EVENT,
                {
                    ...(emailToLowerCase && {
                        user_email: emailToLowerCase,
                        user_hashed_email: sha256(emailToLowerCase),
                    }),
                    ...(phoneToLowerCase && {
                        user_phone_number: phoneToLowerCase,
                        user_hashed_phone_number: sha256(phoneToLowerCase),
                    }),
                }
            );
            sendFacebookEvent(
                FacebookEvents.FORM_SUBMIT_FACEBOOK_SCHEDULED_EVENT,
                {
                    ...(fullName && {
                        first_name: fullName[0].toLowerCase(),
                        last_name: fullName.slice(1).join(" ").toLowerCase(),
                    }),
                    ...(email && {
                        email: emailToLowerCase,
                    }),
                    ...(phone && {
                        phone_number: phoneToLowerCase,
                    }),
                },
                {
                    eventID,
                }
            );

            sendConversionApiEvent(eventID);
        }
    }, [calendlyMessage]);
};

export const useCalendlyEventRedirect = ({
    eventId,
    inviteeId,
    clinicId,
    hasExpireStatus,
    version,
}: Partial<CalendlyNotificationType> & {
    hasExpireStatus: boolean;
    version: CountryVersion;
}) => {
    useEffect(() => {
        if (eventId && inviteeId && hasExpireStatus) {
            const fetchCalendlyEventData = async () => {
                const eventData = await getCalendlyEventData({
                    eventId,
                    inviteeId,
                });

                if (eventData) {
                    const queryObjectCalendly = convertCalendlyNotificationQueryObject(
                        {
                            ...eventData,
                            clinicId,
                        }
                    );

                    const queryStringCalendly = convertQueryObjectToQueryString(
                        queryObjectCalendly as QueryObject
                    );

                    window.location.href = `${
                        VersionToNewThanksPage[version]
                    }?${encodeURI(queryStringCalendly)}`;
                } else {
                    window.location.href = VersionToNewThanksPage[version];
                }
            };

            fetchCalendlyEventData();
        }
    }, [eventId, inviteeId, hasExpireStatus]);
};
